export default {
  data() {
    return {
      swiperOptions: {
        slidesPerView: 'auto',
        speed: 400,
        grab: true
      },
      hideDoctors: false,
      modalOpened: false
    }
  },
  computed: {
    swiper() {
      return this.$refs.doctorsSwiper.$swiper
    }
  },
  methods: {
    nextSlide: function () {
      if (this.swiper.isBeginning && !this.hideDoctors) {
        this.hideDoctors = true;
      } else {
        this.swiper.slideNext();
      }
    },
    prevSlide: function () {
      if (this.swiper.isBeginning && this.hideDoctors) {
        this.hideDoctors = false;
      } else {
        this.swiper.slidePrev();
      }
    }
  }
}
